body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #E08709, 5;
  cursor: pointer;
  color: #FFFFFF;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #E08709, 5;
  cursor: pointer;
  color: #FFFFFF;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #71B100, 5;
  cursor: pointer;
  color: #FFFFFF;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-verde-block:hover {
  background-color: #71B100, 5;
  cursor: pointer;
  color: #FFFFFF;
}

.boton-rojo-tabla {
  background-color: #ff4040;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-rojo-tabla:hover {
  background-color: #ff4040, 5;
  cursor: pointer;
  color: #FFFFFF;
}

.boton-amarillo-tabla {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-tabla:hover {
  background-color: #E08709, 5;
  cursor: pointer;
  color: #FFFFFF;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p, .dark-mode tr {
  color: #FFFFFF;
}
.dark-mode button, .dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}
.dark-mode table.propiedades tbody {
  background-color: #333333;
}
.dark-mode table.propiedades th {
  border-bottom: 0.01x solid rgb(75, 73, 73);
  border-left: 0.01px solid rgb(75, 73, 73);
  border-right: 0.01px solid rgb(75, 73, 73);
  border-top: 0.1px solid rgb(75, 73, 73);
}
.dark-mode table.propiedades td {
  border-bottom: 1px solid rgb(75, 73, 73);
  border-left: 0.1px solid rgb(75, 73, 73);
  border-right: 0.1px solid rgb(75, 73, 73);
  border-top: 1px solid transparent;
}

/**UI**/
.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin: 0;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
  align-items: center;
  padding-left: center;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.logo {
  height: 5rem;
  position: relative;
  top: 2rem;
}

.logo-header {
  max-width: 100%;
  width: 28rem;
  height: auto;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  align-items: center;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
    padding: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
    width: 13rem;
  }
}
.navegacion a:hover {
  color: #71B100;
}
.navegacion .btn-iniciar {
  color: red;
  border: #ff4040 1px solid;
  height: 3.5rem;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column-reverse;
  padding: 1rem;
  margin-bottom: 1rem;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
  padding-top: 2rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 5rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: #FFFFFF;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

/**Paginas Internas**/
@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #ff4040;
}
.alerta.exito {
  background-color: #71B100;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  border-collapse: collapse;
  text-align: center;
}
table.propiedades thead {
  background-color: #3e86b3;
}
table.propiedades thead th {
  padding: 1.5rem;
  border-block-start: 0.1px solid #FFFFFF;
  border-left: 0.5px solid #fff;
  border-right: 0.5px solid #fff;
}
table.propiedades .imagen-tabla {
  display: inline-block;
  width: 20rem;
}
table.propiedades tbody {
  background: #e3eef1;
}
table.propiedades tbody td {
  border-bottom: 1px solid #fff;
  border-left: 0.5px solid #fff;
  border-right: 0.5px solid #fff;
  border-top: 1px solid transparent;
}
table.propiedades .titulo-tabla {
  text-align: justify;
  padding-left: 1rem;
}

.imagen-small {
  width: 20rem;
  margin: 2rem;
}

.amarillo-tabla {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  justify-content: space-evenly;
  width: 12rem;
  line-height: 2rem;
  margin-bottom: 1.5rem;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  padding-right: 2rem;
  background-color: #E08709;
  color: #000000;
  border: none;
  height: 3rem;
}

.rojo-tabla {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  height: 3rem;
  width: 12rem;
  line-height: 2rem;
  margin-bottom: 1.5rem;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  padding-right: 2rem;
  background-color: #ff4040;
  color: #000000;
  font-weight: 400;
  border: none;
  margin-block: 1rem;
}

.admin-header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}

.inicio-sesion {
  background-image: url(../img/background2.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
}

.main-section {
  margin: 0 auto;
  margin-top: 15rem;
  padding: 0;
  width: 30rem;
}

.modal-content {
  background-color: #3b4652;
  opacity: 0.85;
  padding: 20px;
  box-shadow: 0px 0px 5px #fa4a0f;
  width: 40rem;
}

.user-img img {
  width: 200px;
  height: 200px;
  border: solid 2px #ff5c26;
  border-radius: 50%;
  display: inline-block;
  margin-top: -11rem;
